<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-employee-families',
  props: {
    id: { required: true, type: Number }
  },
  data () {
    return {
      config: {
        title: 'Keluarga Tenaga Kerja',
        model_api: 'employee-families',
        getter: 'employee-families',
        setter: 'employee-families',
        pk_field: null,
        filter_api: {
          employee_id: this.id
        },
        permission: {
          create: 'create-employee-families',
          read: 'view-employee-families',
          update: 'update-employee-families',
          delete: 'delete-employee-families'
        },
        slave: [],
        fields: [
          { id: 'is_bpjs_tambahan', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'employee_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'fullname', label: 'Nama Lengkap' },
          { id: 'nik', label: 'NIK' },
          { id: 'telephone', label: 'No. Telp' },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'place_of_birth', label: 'Tempat Lahir' },
          { id: 'date_of_birth', label: 'Tanggal Lahir', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          {
            id: 'gender',
            label: 'Jenis Kelamin',
            methods: {
              list: { transform: 'kelamin' },
              detail: { transform: 'kelamin' },
              create: {
                type: 'radio',
                value: 'male',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              }
            }
          },
          {
            id: 'education_id',
            label: 'Pendidikan Terakhir',
            methods: {
              create: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          // {
          //   id: 'employee_id',
          //   label: 'Nama Tenaga Kerja',
          //   methods: {
          //     create: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     update: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     filter: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     }
          //   }
          // },
          // {
          {
            id: 'family_relationship_id',
            label: 'Hubungan Keluarga',
            methods: {
              create: {
                setter: 'master-family-relationships',
                getter: 'master-family-relationships',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-family-relationships',
                getter: 'master-family-relationships',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-family-relationships',
                getter: 'master-family-relationships',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



